.App {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.container {
  background-color: #f0f0f0;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
  
}



h2 {
  color: #333;
}

form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.inputweight,
.inputheight {
  margin: 10px 0;
  display: flex;
  align-items: center;
}

label {
  margin-right: 10px;
  color: #555;
}

input,
select {
  padding: 8px;
  width: 200px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-right: 10px;
}

.select-container {
  display: flex;
  align-items: center;
}

button {
  padding: 10px;
  background-color: #4caf50;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 20px;
}

button:hover {
  background-color: #45a049;
}

.center {
  margin-top: 20px;
}

h3 {
  color: #333;
}

p {
  color: #555;
}
